export default function (arr, num) {
  let start = 0
  let end = arr.length - 1
  while (start <= end) {
    let middle = Math.floor((start + end) / 2)
    let guess = arr[middle]
    if (guess.id == num) {
      return guess
    }
    if (guess.id > num) {
      end = middle - 1
    } else {
      start = middle + 1
    }
  }
  return ''
}