import { manager } from '../../../components/全局事件挂载/index.js'
import { onMounted } from 'vue'
export default function (black_wall) {
  //这里是所有导入的 Element
  let back_wall_Element = null

  const move_to = (e) => {
    return new Promise((resolve) => {
      if (e.deltaY > 0) {
        resolve()
      }
      if (e.type === 'click') {
        resolve()
      }
    }).then(() => {
      manager.emit('change_index_page', true)
      back_wall_Element.style.bottom = -200 + 'px'
    })
  }

  onMounted(() => {
    back_wall_Element = black_wall.value
  })

  return { move_to }
}
