import Search from "../../../components/查找算法/二分查找.js"
import UserStore from "../../../store/UserStore.ts"
export default function(use_IndexBackground){
  const use_UserStore = UserStore()
  const change_page =(e)=>{
    let id = e.target.getAttribute('data-zxcv')
    let result_data = Search(use_IndexBackground.BackgroundImage,id)
    use_UserStore.ChangeBackground(result_data.imgUrl)
  }

  return {change_page}
}








