import { watch, onMounted, ref } from 'vue'
export default function (All_page) {
  let All_page_element = null
  let click = ref(0)

  watch(click, (newSum) => {
    if (newSum % 2 == 0) {
      All_page_element.style.setProperty('height', '0px')
      All_page_element.style.setProperty('opacity', '0')
    } else {
      All_page_element.style.setProperty('display', 'block')
      All_page_element.style.setProperty('height', '65vmin')
      All_page_element.style.setProperty('opacity', '1')
    }
  })

  onMounted(() => {
    All_page_element = All_page.value
  })

  return { click }
}
