import { defineStore } from 'pinia'
import { inject } from 'vue'
export default defineStore({
  id: 'IndexBackground',
  state: () => {
    return {
      Axiosagain:true,
      BackgroundImage:{},
      SmBackgroundImage:{}
    }  
  },
  
  getters: {
  },
  actions: {
    async send_backgroundAxios(){
      const axios:any = inject('$axios')
      //获取当前用户使用的是手机还是电脑
      let UserUse = localStorage.getItem('UserUse') ? 'computer' : 'mobile'
      let resultValue = await axios.get("/images/give",{
        params:{type:UserUse}
      })
      this.$patch(state => {
        state.BackgroundImage = resultValue.data.BackgroundImage
        state.SmBackgroundImage = resultValue.data.SmBackgroundImage
        state.Axiosagain = false
      })
    }
  }
})