import UserStore from '@/store/UserStore'

export default function (use_IndexBackground) {
  //拿取用户对应的数据, 背景图片就是在这里设置的
  const use_UserStore = UserStore()
  //pinia 依靠 了 locaStorage 进行数据持久化, 但是在这里是时数据还未存储到 pinia 
  //这里是为了防止用户刷新之后又重新检测一次, 直接 return 即可
  if (JSON.parse(localStorage.getItem('UserStore'))?.Background_URL) return
  //这里需要设置默认图片, 那么就只能进行检测赋值
  let timer = null
  timer = setInterval(() => {
    //store文件会先发起请求拿到对应的图片, 那么这里就是将图片设置为背景图, 如果设置成功就退出检测
    let backgroundUrl = use_IndexBackground.BackgroundImage[0].imgUrl
    use_UserStore.ChangeBackground(backgroundUrl)
    if (use_IndexBackground.BackgroundImage[0].imgUrl) {
      clearInterval(timer)
      timer = null
      return
    }
  }, 1000)
  //给予最大生存时间, 防止因为背景图未出现导致一直报错
  setTimeout(()=>{clearInterval(timer)},5000)
}
